.InputConfig input{
    font-family: 'Pridi', serif;
    font-size: 1.3em;
    display: block;
    padding: 2px 3px 2px 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.InputConfig select {
    padding: 2px 3px 2px 10px;
    margin-bottom: 10px;
    font-size: 1.3em;
    font-family: 'Pridi', serif;
    border-radius: 5px;
}

.InputConfig button {
    font-size: 1.3em;
    font-family: 'Pridi', serif;
    color: whitesmoke;
    background: #009688;
    border: none;
    border-radius: 5px;
    padding: 10px;
    width: 120px;
    text-align: center;
    transition: all 0.8s;
    cursor: pointer;
}

.InputConfig button:hover {
    background: #8BC34A;
}

.InputConfig button:disabled {
    background: #f44336;
    cursor: not-allowed;
}